<template>
  <div id="OBSOvertimeWork">
    <div id="timeLeftShow">
      <h5>加班时间</h5>
      <span>{{ currentWorkTime | timeFormat }}</span>
    </div>
    <div id="modItemList">
      <h5>礼物列表</h5>
      <b-row>
        <template v-for="(item,index) in modList">
          <b-col :key="index+'key'" cols="4" class="   ">
            <b-container class="modListDataShow" style="position: relative; padding: 0">
              <b-img-lazy v-if="item.img" :src="item.img" class="modListDataImg"/>
              <span v-else>{{ item.giftName }}</span>
              <span class="modListItemCount">{{ item.count !== 1 ? "x" + item.count : "" }}</span>
            </b-container>
            <p class="modListDataDesc">{{ item | modTypeFormat }}</p>
          </b-col>
        </template>
        <template v-if="modList.length === 0">
          <b-container>
            <br>
            <p style="color: white;font-size: 30px">当前礼物列表为空</p>
          </b-container>
        </template>
      </b-row>
    </div>
  </div>
</template>

<script>
import Tools from "@/common/ToolsFunctions";

export default {
  name: "ViewOvertimeWork",
  data() {
    return {
      currentWorkTime: 7213211531,
      modList: [],
      tempGiftList: [],
    }
  },
  methods: {
    async getBaseInfo() {
      let result = await Tools.sendRequest.call(this, "/API/overtimeWork/getInfo");
      if (result.err) return;
      this.currentWorkTime = result.data.currentWorkTime;
      this.modList = result.data.modList;
      this.tempGiftList = result.data.tempGiftList;
      this.$forceUpdate();
    }
  },
  mounted() {
    // 获取基础信息
    this.getBaseInfo();
    // 关注剩余时间变动
    this.$EventBus.$on("OvertimeWork-changeTime", data => {
      data = JSON.parse(data);
      this.currentWorkTime = data['timeLeft'];
    });
    // 关注加班道具列表变动
    this.$EventBus.$on("OvertimeWork-changeModList", data => {
      data = JSON.parse(data);
      this.modList = data['data'];
    });
    // 自动降低时长
    setInterval(() => {
      if (this.currentWorkTime <= 0 || !this.$store.state.liveStatus) return;
      this.currentWorkTime--;
    }, 1000);
  },
  computed: {},
  filters: {
    timeFormat(input) {
      let output = ``;
      output += Math.floor(input / 86400) + "天 ";
      input -= Math.floor(input / 86400) * 86400;
      output += Math.floor(input / 3600) + "小时 ";
      input -= Math.floor(input / 3600) * 3600;
      output += Math.floor(input / 60) + "分钟 ";
      input -= Math.floor(input / 60) * 60;
      output += Math.floor(input) + "秒";
      return output;
    },
    modTypeFormat(input) {
      let output = "";
      switch (input.type) {
        case "add":
          output = `加班时间 增加${input.timeChange}秒`;
          break;
        case "reduce":
          output = `加班时间 减少${input.timeChange}秒`;
          break;
        case "multi":
          output = `加班时间 乘以${input.timeChange}`;
          break;
        case "division":
          output = `加班时间 除以${input.timeChange}`;
          break;
        case "power":
          output = `剩余时间 ${input.timeChange}次方`;
          break;
      }
      return output;
    }
  },
}
</script>

<style scoped>
#OBSOvertimeWork * {
  font-family: "Microsoft YaHei UI", sans-serif;
}

#timeLeftShow,
#modItemList {
  padding: 10px 5px 10px 5px;
  margin-top: 10px;
  width: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
}

#timeLeftShow > h5,
#modItemList > h5 {
  color: white;
}

#timeLeftShow > span {
  font-size: 23px;
  color: white;
}

.modList > div {
  color: white;
  font-size: 14px;
}

.modListData {
  text-align: center;
  color: white;
}

.modListDataImg {
  height: 100%;
}

.modListDataShow {
  overflow: hidden;
  height: 50px;
  width: 50px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 10%;
}

.modListDataShow > span {
  font-family: "Microsoft YaHei UI", sans-serif;
}

.modListItemCount {
  font-weight: bold;
  color: rgba(0, 0, 0, 0.8);
  z-index: 999;
  position: absolute;
  top: 27px;
  right: 0;
  border-radius: 2px;
}

.modListDataDesc {
  font-size: 13px;
  color: white;
}
</style>