import axios from "axios";
import {showToast} from "@/common/ToastShow";

/**
 *
 * @param url {String} 访问的地址
 * @param method {String} 访问的方法
 * @return {Promise<{err: boolean, data: *} | {err: boolean}>}
 */
function sendRequest(url, method = "get") {
  return axios({
    url, method
  }).then(axiosRes => {
    return {err: false, data: axiosRes.data};
  }).catch(axiosErr => {
    showToast.call(this, "请求出现错误", axiosErr.message, "danger");
    return {err: true};
  })
}

export default {sendRequest};